<template>
  <view class="home-wrap">
    <!-- <image src=""></image> -->
    <swiper class="swiper"
            :indicator-dots="indicatorDots"
            :autoplay="autoplay"
            :interval="interval"
            :duration="duration">
      <swiper-item v-for="(item,index) in bannerList"
                   :item-id="String(index)"
                   :key="index">
        <image mode="heightFix"
               class="swiper-image"
               :src="item&&item.imageApp"></image>
      </swiper-item>

    </swiper>
    <WybNoticeBar :text="broadcastData"
                  bgColor="#0D183B"
                  color="#ECECF5"
                  type="vert"></WybNoticeBar>
    <view>

    </view>
  </view>
</template>
<script>
import subMarket from '@/assets/mixins/subMarket'
import WybNoticeBar from '@/components/wyb-noticeBar/wyb-noticeBar.vue'
import { mapState } from 'vuex'
export default {
  name: "home",
  data () {
    return {
      broadcastData: [],
      bannerList: [],
      indicatorDots: true,
      autoplay: true,
      interval: 2000,
      duration: 500
    };
  },
  components: { WybNoticeBar },
  mixins: [subMarket],
  computed: {
    ...mapState({
      manifest: state => state.base.manifest || {}
    }),
    headerUrl () {
      const { url } = this.manifest || {}
      const { h5Url } = url || {}
      const str = String(h5Url) && String(h5Url).split('.')
      return str
    }
  },
  watch: {
    headerUrl (val) {
      if (!val) return
      this.setTitle()
      this.fetchBannerList(true)
    }
  },
  methods: {
    async fetchBannerList (showError) {
      const params = {
        languageCode: this.$i18n.locale,
        site: '亚太站'
      }
      const arr = await this.$http.post('v1/site/getBannerList', params, { showError }).catch(() => {
        const id = setTimeout(() => {
          this.fetchBannerList()
          clearTimeout(id)
        }, 5000);
      })
      this.bannerList = arr || []
      this.initList(arr)
    },
    initList (list) {
      if (!list || this.$_.isEmpty(list)) return
      list.forEach(element => {
        const { title } = element || {}
        this.broadcastData.push(title)

      });
      console.log('--notice--', this.broadcastData);
    },
    setTitle () {
      const [, header, headerPro] = this.headerUrl || []
      const _header = `${header}.${headerPro}`
      uni.setNavigationBarTitle({
        title: _header
      });
    }
  },

  onShow () {
  }
}
</script>
<style lang="less" scoped>
.home-wrap {
  .swiper {
    height: 220rpx;
    width: 100%;
    justify-content: center;
    display: flex;
  }
  .swiper-image {
    height: 200rpx;
    padding-top: 20px;
    // width: 100%;
  }
}
</style>